


























































































































.drawer-fade-enter-active {
  transition: all .3s ease;
}
.drawer-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.drawer-fade-enter, .drawer-fade-leave-to{
  transform: translateX(60px);
  opacity: 0;
}
